.User {
  display: flex;
  align-items: flex-start;
  padding: 1rem;
  position: relative;
}

.User:hover {
  background: rgba(0, 0, 0, 0.8);
  cursor: pointer;
}

.User__pic {
  width: 50px;
  border-radius: 50%;
}

.User__details {
  display: none;
}

.User__closeIcon {
  position: absolute;
  top: 0px;
  right: 3px;
}

@media (min-width: 576px) {
  .User__details {
    display: block;
    padding: 0 0 0 1rem;
  }
  .User__details-name {
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    font-size: 1.2rem;
  }
}
